<script>
export default {
    name: 'Header',
    data() {
        return {
            menuopen: false,
            textColor: "white",
            upscroll: false,
            lastScrollTop: 0
        }
    },
    mounted: function () {
        if(this.$route.meta.theme) this.colorChange();
        window.addEventListener("scroll", this.scroll, false);
    },
    methods: {
        scroll: function () {
            let st = window.pageYOffset || document.documentElement.scrollTop;

            if(st > 0 && st < this.lastScrollTop) {
                // upscroll code
                this.upscroll=true;
            } else {
                // downscroll code
                this.upscroll=false;
            }
            this.lastScrollTop = st;
        },
        colorChange: function () {
            this.textColor = this.$route.meta.theme;
        },
        mouseMenu: function(){
            this.menuopen = !this.menuopen;
        },
    },
    watch:{
        $route (){
            this.menuopen=false;
            this.colorChange();
        }
    },
    computed: {
        colorStyle() {
            return {
                '--text-color': this.textColor,
            }
        }
    }
}
</script>

