import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router);

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior() {
    return { y: 0 };
  },
  routes: [
    {
      path: '/',
      name: 'home',
      meta: { theme: 'white' },
      component: Home,

      props: (route) => ({ query: route.query.player })
    },
    {
        path: '/portfolio',
        name: 'Portfolio',
        meta: { theme: 'black' },
        component: () => import('./views/Portfolio.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/quisuisje',
        name: 'QuiSuisJe',
        meta: { theme: 'black' },
        component: () => import('./views/QuiSuisJe.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
      path: '/votreplusbeaumarche',
      name: 'VotrePlusBeauMarche',
      meta: { theme: 'black' },
      component: () => import('./views/VotrePlusBeauMarche.vue'),
      props: (route) => ({ query: route.query.player })
    },
    {
      path: '/carredamour',
      name: 'CarreDAmour',
      meta: { theme: 'black' },
      component: () => import('./views/CarreDAmour.vue'),
      props: (route) => ({ query: route.query.player })
    },
    {
      path: '/delgaudiogourmet',
      name: 'DelGaudioGourmet',
      meta: { theme: 'black' },
      component: () => import('./views/DelGaudioGourmet.vue'),
      props: (route) => ({ query: route.query.player })
    },
    {
      path: '/emling',
      name: 'Emling',
      meta: { theme: 'black' },
      component: () => import('./views/Emling.vue'),
      props: (route) => ({ query: route.query.player })
    },
    {
      path: '/familydrugstore',
      name: 'FamilyDrugstore',
      meta: { theme: 'black' },
      component: () => import('./views/FamilyDrugstore.vue'),
      props: (route) => ({ query: route.query.player })
    },
    {
        path: '/logotype',
        name: 'Logotype',
        meta: { theme: 'black' },
        component: () => import('./views/Logotype.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
      path: '/contact',
      name: 'contact',
      meta: { theme: 'white' },
      component: () => import('./views/Contact.vue'),
      props: (route) => ({ query: route.query.player })
    },
    {
     path: '/404',
     name: '404',
     meta: { theme: 'white' },
     component: () => import('./views/404.vue'),
     props: (route) => ({ query: route.query.player })
    },  
    { path: '*', redirect: '/404' },  
  ]
})
