<template>
  <div id="app" couleur="couleur">
    <Header id="header"/>
    <thePlayer v-if="myplayer"/>
    <router-view class="viewApp"/>
    <Footer/>
  </div>
</template>

<script>
    // @ is an alias to /src
    import thePlayer from '@/components/Player.vue'
    import Header from '@/components/Header.vue'
    import Footer from '@/components/Footer.vue'

    export default {
        name: 'app',
        props: {
            couleur: String
        },
        data() {
            return {
                lien: 'w00t',
                myplayer: false,
            }
        },
        components: {
            thePlayer,
            Header,
            Footer
        },
        methods: {
            linkPlayer: function(lienPlayer) {
                this.lien = lienPlayer;
                this.myplayer=true;
            },
        },
        mounted: function () {
            let URLPlayer = this.$route.query.player;
            if(URLPlayer) {
                this.linkPlayer(URLPlayer);
            }

            let URLRoute = this.$route.query.route;
            console.log(URLRoute);
            if(URLRoute) {
                this.$router.push(URLRoute);
            }
        },
    }
</script>

<style lang="scss">
  @import "../node_modules/bootstrap/scss/bootstrap";
  @import "./assets/scss/custom";
</style>

<style lang="scss" scoped>
    #app{
      position: relative;
      min-height: 100%;

      .viewApp{
        min-height: 100%;
      }
    }

</style>